.page {
  padding: var(--inner-padding);
  background-color: var(--background-color);
  color: var(--default-color);

  >h1,
  >p {
    text-align: center;
  }

  >p {
    margin-bottom: 48px;
  }
}

.posts-container {
  display: flex;
  flex-wrap: wrap;

  align-items: center;
  justify-content: center;

  margin-bottom: 84px;

  .project-card:hover {
    background-color: #3498db99;
  }
}

.project-cards-container {
  position: relative;
  min-height: 100px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 24px;

  .project-card {
    border: 1px solid black;
    border-radius: 8px;
    max-width: 600px;
    margin: 0 0px 24px 24px;
    padding: 1em;
    display: flex;
    flex-direction: column;

    >p {
      flex-grow: 1;
    }


    >span:first-child {
      font-size: 0.8em;
      font-weight: 200;
    }

    h1 {
      font-weight: bold;
      font-size: 1.2em;
      margin-bottom: 0px;
    }

    a {
      text-decoration: none;
      color: var(--default-color);
    }

    .tags-container {
      span {
        margin-right: 8px;
        font-weight: bold;
      }
    }
  }

  .project-card:hover {
    background-color: #3498db11;
  }
}

.loader {
  position: absolute;
  left: 50%;
  top: 90%;
  transform: translate(-50%, -50%);
  width: 200px;
  height: 200px;
  text-align: center;
  font-weight: bold;
  color: #333;

  .loader-positioning {
    position: absolute;
    left: 45%;
    transform: translateX(-50%);
    top: 48px;
  }

  .lds-dual-ring {
    display: inline-block;
    width: 80px;
    height: 80px;
  }

  .lds-dual-ring:after {
    content: " ";
    display: block;
    width: 64px;
    height: 64px;
    margin: 8px;
    border-radius: 50%;
    border: 6px solid #666;
    border-color: #666 transparent #666 transparent;
    animation: lds-dual-ring 1.2s linear infinite;
  }

  @keyframes lds-dual-ring {
    0% {
      transform: rotate(0deg);
    }

    100% {
      transform: rotate(360deg);
    }
  }
}