:root{--primary-color: #3498db;--background-color: #F8F9FA;--default-color: #2d3436;--translucent-dark: #2d3436aa;--inner-padding: 24px;--inner-margin: 24px;--max-width: 1080px;box-sizing:border-box}:root h1,:root h2,:root p,:root span{color:#2d3436;color:var(--default-color)}html{box-sizing:border-box}*,*:before,*:after{box-sizing:inherit}.page{margin-top:91px;width:100%}body{font-family:"Roboto";margin:0px;padding:0px;min-width:-webkit-fit-content;min-width:-moz-fit-content;min-width:fit-content}


.page{padding:var(--inner-padding);background-color:var(--background-color);color:var(--default-color)}.page>h1,.page>p{text-align:center}.page>p{margin-bottom:48px}.posts-container{display:flex;flex-wrap:wrap;align-items:center;justify-content:center;margin-bottom:84px}.posts-container .project-card:hover{background-color:#3498db99}.project-cards-container{position:relative;min-height:100px;display:flex;flex-wrap:wrap;justify-content:center;margin-top:24px}.project-cards-container .project-card{border:1px solid black;border-radius:8px;max-width:600px;margin:0 0px 24px 24px;padding:1em;display:flex;flex-direction:column}.project-cards-container .project-card>p{flex-grow:1}.project-cards-container .project-card>span:first-child{font-size:0.8em;font-weight:200}.project-cards-container .project-card h1{font-weight:bold;font-size:1.2em;margin-bottom:0px}.project-cards-container .project-card a{text-decoration:none;color:var(--default-color)}.project-cards-container .project-card .tags-container span{margin-right:8px;font-weight:bold}.project-cards-container .project-card:hover{background-color:#3498db11}.loader{position:absolute;left:50%;top:90%;transform:translate(-50%, -50%);width:200px;height:200px;text-align:center;font-weight:bold;color:#333}.loader .loader-positioning{position:absolute;left:45%;transform:translateX(-50%);top:48px}.loader .lds-dual-ring{display:inline-block;width:80px;height:80px}.loader .lds-dual-ring:after{content:" ";display:block;width:64px;height:64px;margin:8px;border-radius:50%;border:6px solid #666;border-color:#666 transparent #666 transparent;-webkit-animation:lds-dual-ring 1.2s linear infinite;animation:lds-dual-ring 1.2s linear infinite}@-webkit-keyframes lds-dual-ring{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}@keyframes lds-dual-ring{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}

.post-card{width:600px;min-height:200px;flex-wrap:wrap;display:flex;flex-direction:row;background-color:white;position:relative;margin:calc(var(--inner-margin) / 2);box-shadow:5px 5px 9px -3px rgba(0,0,0,0.15);transition:0.5s;cursor:pointer}.post-card .post-photo{order:2;flex-grow:1.5;background-position:center;background-size:cover}.post-card .post-body{order:1;flex-grow:2;flex-basis:0;padding:var(--inner-padding);display:flex;flex-direction:column}.post-card .post-body p{flex-grow:1}.post-card .post-body h1{font-weight:bold;font-size:1.2em;margin:0px}.post-card .post-body small a{font-weight:bold;text-decoration:none;color:var(--default-color)}.post-card .post-body small:nth-child(even){display:block;margin-top:4px;font-weight:300}.post-card:hover{box-shadow:5px 5px 12px -3px rgba(0,0,0,0.4);transition:0.5s}

.footer-container{display:flex;flex-direction:column;justify-content:center;align-items:center;min-height:200px}.footer-container>div{display:flex;width:100%;justify-content:center;align-items:center}.footer-container>div a{font-size:2em;text-decoration:none;color:var(--default-color);margin:12px}

.summary-section{background-color:var(--background-color);min-height:calc(85vh - 91px);display:flex;align-items:center;padding-left:16px;padding-right:16px}.summary-section .inner-space{width:100%;max-width:1280px;margin:0 auto;background:url("/resources/main_background.svg");background-size:cover;display:flex;align-items:center;flex-wrap:wrap}.summary-section .personal-summary{flex-grow:1;flex-basis:0px;text-align:right;display:flex;flex-direction:column;justify-content:flex-end;align-items:flex-end;min-width:500px}.summary-section .personal-summary p{text-align:right;max-width:500px}.summary-section .personal-summary>span{font-weight:bold;color:var(--primary-color);display:inline-block}.summary-section .personal-summary small{color:#666666;font-weight:bold;font-size:1em}.summary-section .personal-summary h1{margin-top:0px}.summary-section .header-background{flex-grow:1.3;min-height:450px;background:url("/resources/blob-shape.svg");background-position:center;background-repeat:no-repeat;min-width:550px}.fadeIn{-webkit-animation-name:fadeIn;animation-name:fadeIn;-webkit-animation-duration:1s;animation-duration:1s}@-webkit-keyframes fadeIn{from{opacity:0}to{opacity:1}}@keyframes fadeIn{from{opacity:0}to{opacity:1}}@media (max-width: 1081px){.summary-section .inner-space .personal-summary{order:2;text-align:center;justify-content:center;align-items:center}.summary-section .inner-space .personal-summary p{text-align:center}.summary-section .inner-space .header-background{order:1;background-position:45% 50%;margin-bottom:24px}}@media (max-width: 581px){.summary-section .inner-space .personal-summary{min-width:250px}.summary-section .inner-space .header-background{min-width:300px}}

.knowledge-areas{width:100%;margin:48px 0;display:flex;flex-direction:column;align-items:center;justify-items:center}.knowledge-areas>.title{text-align:center;max-width:400px}.knowledge-areas .specialization-carrousel{display:flex;max-width:980px;margin-top:24px;width:100%;align-items:center;justify-content:space-evenly;flex-wrap:wrap}@media (max-width: 930px){.knowledge-section{flex-direction:column}.knowledge-section .section-header{min-width:200px}.knowledge-section .section-body{min-width:200px}}

.specialization-card-container{display:flex;flex-direction:column;max-width:300px;align-items:center;text-align:center;padding:24px;margin:12px 0 12px 0;transform:translateY(50px);transition:transform 3s 0.25s cubic-bezier(0, 1, 0.3, 1),opacity 0.5s 0.25s ease-out;width:90%;will-change:transform, opacity;opacity:0}.specialization-card-container img{width:150px}.specialization-card-container h3{font-weight:bold;margin-bottom:0px}.highlighted{border-radius:12px;background-color:var(--primary-color);box-shadow:10px 10px 13px -6px #ececec;background-image:linear-gradient(-160deg, #3498DB 35%, #9B59B6 100%)}.highlighted h3,.highlighted p,.highlighted span{color:white}.is-visible{transform:translateY(0px);opacity:1}@-webkit-keyframes slideIn{from{padding:68px}to{padding:28px}}@keyframes slideIn{from{padding:68px}to{padding:28px}}

@use "sass:map";.last-works-title{width:100%;max-width:400px;padding:var(--inner-padding);margin:auto;text-align:center}@-webkit-keyframes ripple{from{background-size:0px;background-position:0 100%}to{background-size:1000px;background-position:-400px calc(100% + 500px)}}@keyframes ripple{from{background-size:0px;background-position:0 100%}to{background-size:1000px;background-position:-400px calc(100% + 500px)}}.last-works-container{width:100%;max-width:var(--max-width);padding:var(--inner-padding);display:flex;align-items:stretch;height:500px;margin:auto;flex-wrap:wrap;box-sizing:content-box}.last-works-container .column:nth-child(1) .cell:nth-child(1){background-color:#1abc9c;flex-grow:1.1;flex-basis:0px;background-image:url("/resources/last-works/trampoline_background.svg");background-size:cover;background-position:center}.last-works-container .column:nth-child(1) .cell:nth-child(2){background-color:#2ecc71;flex-grow:1.5;flex-basis:0px;background-image:url("/resources/last-works/buscoextra_background.jpg");background-size:cover;background-position:center}.last-works-container .column:nth-child(1) .cell:nth-child(3){background-color:#3498db;flex-grow:1.3;flex-basis:0px;background-image:url("/resources/last-works/tomarani.png");background-size:cover;background-position:center -50px}.last-works-container .column:nth-child(2) .cell:nth-child(1){background-color:#9b59b6;flex-grow:1.3;flex-basis:0px;background-image:url("/resources/last-works/willimbu_background.jpg");background-size:cover;background-position:center}.last-works-container .column:nth-child(2) .cell:nth-child(2){background-color:#34495e;flex-grow:1.1;flex-basis:0px;background-image:url("/resources/last-works/medium_background.png");background-size:cover;background-position:center}.last-works-container .column:nth-child(2) .cell:nth-child(3){background-color:#f1c40f;flex-grow:1;flex-basis:0px;background-image:url("/resources/last-works/qbbo_background.png");background-size:cover;background-position:center}.last-works-container .column:nth-child(3) .cell:nth-child(1){background-color:#3498db;flex-grow:1;flex-basis:0px;background-image:url("/resources/last-works/tomarani.png");background-size:cover;background-position:center}.last-works-container .column:nth-child(3) .cell:nth-child(2){background-color:#e74c3c;flex-grow:1;flex-basis:0px;background-image:url("/resources/last-works/qbbo_background.png");background-size:cover;background-position:center}.last-works-container .column:nth-child(3) .cell:nth-child(3){background-color:#e67e22;flex-grow:1;flex-basis:0px;background-image:url("/resources/last-works/angular_malaga_background.jpg");background-size:cover;background-position:center}.last-works-container .cell{border-radius:12px;margin:8px;box-sizing:border-box}.last-works-container .cell .inner-content{height:100%;width:100%;border-radius:inherit;background:url("/resources/last-work_background.svg");background-repeat:no-repeat;background-size:0px;background-position:0 100%;display:flex;flex-direction:column;justify-content:flex-end}.last-works-container .cell .inner-content>div{transition:0.5s;background-color:var(--translucent-dark);width:100%;height:20px;padding:var(--inner-padding);border-radius:inherit;border-top-left-radius:0px;border-top-right-radius:0px;display:flex;align-items:flex-start;flex-direction:column;overflow:hidden}.last-works-container .cell .inner-content>div h3{color:white;margin:-10px 0 4px -5px}.last-works-container .cell .inner-content>div small{font-weight:bold;color:white;transition:0.5s;opacity:0}.last-works-container .cell .inner-content>div p{transition:1s;color:white;opacity:0;margin:4px 0 0 0}.last-works-container .cell :hover{-webkit-animation:ripple 2s forwards;animation:ripple 2s forwards}.last-works-container .cell :hover>div{height:100%;background-color:var(--traslucent-dark)}.last-works-container .cell :hover>div small{opacity:1}.last-works-container .cell :hover>div p{opacity:1}.last-works-container .column{min-width:250px;flex-grow:1;display:flex;flex-direction:column;height:inherit;flex-basis:0}@media (max-width: 547px){.last-works-container{height:100%}.last-works-container .column{height:350px}.last-works-container .column .cell{flex-grow:1 !important}}

.share-button{height:50px;background:#dfe6e9;border-radius:40px;display:flex;align-items:center;justify-content:center;padding:0 50px;overflow:hidden;position:relative;cursor:pointer;transition:.3s linear}.share-button:hover{transform:scale(1.1)}.share-button span{position:absolute;width:100%;height:100%;text-align:center;line-height:50px;z-index:2;transition:.6s linear;border-radius:40px;background-image:linear-gradient(165deg, #3498DB 35%, #9B59B6 100%);color:white}.share-button:hover span{transform:translateX(-100%);transition-delay:.3s}.share-button a{flex:1 1;font-size:26px;margin-right:20px;color:var(--default-color);text-align:center;transform:translateX(-100%);opacity:0;transition:.3s linear}.share-button:hover a{opacity:1;transform:translateX(0)}.share-button a:nth-of-type(1){transition-delay:1s}.share-button a:nth-of-type(2){transition-delay:.8s}.share-button a:nth-of-type(3){transition-delay:.6s}.share-button a:nth-of-type(4){transition-delay:.4s}

nav{display:flex;align-items:center;justify-content:space-between;position:fixed;width:100%;left:0px;top:0px;padding:0 24px;z-index:10;background-color:var(--background-color);min-height:91px;box-sizing:border-box}nav>a{font-size:1em;font-weight:400;cursor:pointer;color:var(--default-color)}nav .sections{display:flex;align-items:center}nav .sections a,nav .sections span{margin:0px 8px;text-decoration:none;color:var(--default-color);cursor:pointer}nav .sections span{text-align:center;line-height:40px;border-radius:40px;background-image:linear-gradient(165deg, #3498DB 35%, #9B59B6 100%);color:white;padding:0 20px}nav .sections span a{color:white}

